.guarant {
    background: url(../img/gaurantBG.png) no-repeat;
    background-size: cover;
    background-position: center center;
    min-height: 1075px;
    position: relative;
    margin-top: -100px;
    color: #fff;
    .lg-block({
        min-height: auto;
        background: none;
        background-image: @gradient;
        margin-top: 50px;
        padding-bottom: 45px;
    });
    &_title {
        padding: 312px 0 10px;
        .lg-block({
            padding-top: 50px;     
        });
    }
    &_wrap {
        margin-top: 65px;
        display: flex;
        justify-content: space-around;
        text-align: center;
        .lg-block({
            flex-wrap: wrap;
            justify-content: center;
        });
    }
    &_item {
        margin:20px;
        padding: 0 10px;
        border-radius: 10px;
        background: url(../img/just.png) no-repeat;
        position: relative;
        &:nth-child(1)::after {
            content: '';
            position: absolute;
            top: 30px; right: 30px;
            background: url(../img/garafter1.png) no-repeat;
            width: 83px;
            height: 93px;
        }
        &:nth-child(2)::after {
            content: '';
            position: absolute;
            top: 30px; right: 30px;
            background: url(../img/garafter2.png) no-repeat;
            width: 108px;
            height: 106px;
        }
        &:nth-child(3)::after {
            content: '';
            position: absolute;
            top: 30px; right: 30px;
            background: url(../img/garafter3.png) no-repeat;
            width: 118px;
            height: 118px;
        }
        &:nth-child(4)::after {
            content: '';
            position: absolute;
            top: 30px; right: 30px;
            background: url(../img/garafter4.png) no-repeat;
            width: 119px;
            height: 118px;
        }
        &__img {
            padding: 50px 0 35px;
        }
        &__title {
            max-width: 250px;
            line-height: 24px;
            letter-spacing: 1.6px;
        }
    }
    &_btn {
        color: #000;
        text-align: center;
        display: block;
        width: 264px;
        margin: 50px auto 0;
    }
}