.work {
    margin-top: 100px;
    .sm-block({
        margin-top: 0;
    });
    .xxs-block({
        position: relative;
        top: -50px;
    });
    &_title {
        &::after {
            content: 'Программы';
            .h2;
            .md-block({
                display: none;
            });
        }
    }
    &_slider {
        .slick-next {
            right: 5px;
            z-index: 100;
            background: url(../img/arrows-next.png) no-repeat;
            width: 22px;
            height: 34px;
            &::before {
                display: none;
            }
            &:hover{
                background: url(../img/arrows-next.png) no-repeat;
            }
        }
        .slick-prev {
            z-index: 100;
            left: -10px;
            background: url(../img/arrows-prev.png) no-repeat;
            width: 22px;
            height: 34px;
            &::before {
                display: none;
            }
            &:hover{
                background: url(../img/arrows-prev.png) no-repeat;
            }
        }
    }
    &_wrap {
        display: flex;
        .md-block({
            flex-wrap: wrap;
            justify-content: center;
        });
    }
    &_img {
        position: relative;
        top: 50px;
        right: 0;
        .md-block({
            padding-top: 25px;
           width: 637px;
            height: 365px;
            position: static;
        });
    }
    &_boldtext {
        color: #000;
        font-family: @bold, sans-serif;
        font-size: 18px;
        letter-spacing: 1.8px;
    }
    &_normtext {
        line-height: 30px;
        letter-spacing: 1.6px;
    }
    &_two {
        margin-left: 55px;
        margin-top: 45px;
        .md-block({
            margin-left: 20px;
        });
    }
    &_three {
        margin: 30px 0 35px;
        margin-left: 20px;
    }
    &_four {
        margin-left: 20px;
        margin-bottom: 8px;
    }
    &_ul {
        margin-left: 45px;
    }
    &_li {
        padding-left: 15px;
        list-style: disc;
    }
    &_bottom {
        display: flex;
        justify-content: space-between;
        margin-top: 120px;
        .xs-block({
            margin-top: 0px;
        });
        .xxs-block({
            position: relative;
            top: -100px;
        });
        .md-block({
            flex-wrap: wrap;
            justify-content: center;
        });
        &__btn {
            margin-left: 100px;
            .md-block({
                margin-left: 0;
            });
            color: #fff;
            padding: 15px 25px;
            box-shadow: 3px 4px 10px 1px rgba(0, 0, 0, 0.3);
            border-radius: 25px;
            background-color: @main;
            line-height: 30px;
            font-family: @bold, sans-serif;
            transition: .3s all;
            cursor: pointer;
            .xs-block({
                padding: 15px;
                font-size: 14px;
            });
            &:hover {
                transform: translateY(-4px);
            }
        }
        &__left {
            padding: 20px;
        }
        &__right {
            padding: 20px;
            p {
                max-width: 440px;
            }
        }
    }
}