.footer {
    background: url(../img/footerBG.png) no-repeat;
    min-height: 751px;
    background-size: cover;
    background-position: center center;
    margin-top: -410px;
    color: #fff;
    .lg-block({
        min-height: auto;
        background: none;
        background-image: @gradient;
        margin-top: 0px;
    });
    &_title {
        text-align: left;
    }
    &_content {
        padding-top: 360px;
        .row-flex();
        justify-content: space-around;
        .lg-block({
           padding-top: 100px;
        });
        .sm-block({
            justify-content: center;
        });
    }
    &_text {
        .col();
        .size(5);
        .size-sm(8);
        .size-xs(10);
        .size-xxs(12);
        padding: 0 10px;
        p {
            margin-top: 80px;
            max-width: 450px;
            font-size: 18px;
            line-height: 36px;
            .lg-block({
                margin-top: 25px;
            });
        }
    }
    &_form {
        .col();
        .size(5);
        .size-sm(8);
        .size-xs(10);
        .size-xxs(12);
        border-radius: 10px;
        background: rgba(255,255,255,0.5);
        .sm-block({
            margin-top: 25px;
        });
        &__form {
            position: relative;
            padding: 25px 10px 45px;
            color: #060606;
        }
        &__label {
            margin: 25px 0 10px;
            padding: 0 40px;
            display: block;
            color: #060606;
            font-size: 16px;
            line-height: 30px;
            .lg-block({
                font-size: 14px;
                margin-top: 20px;
            });
        }
        &__input {
            width: 70%;
            margin-left: 40px;
            border: 1px solid #aeaeae;
            background-color: #fafafa;
            font-size: 14px;
            padding: 15px;
            color: #1f1f1f;
            .md-block({
                margin-left: 20px;
            });
        }
        &__submit {
            margin: 25px auto 0;
            display: block;
        }
    }
    &_bottom {
        margin-top: 50px;
        padding-bottom: 15px;
        .row-flex();
        justify-content: space-between;
        align-items: center;
        color: #fff;
        line-height: 1;
        font-size: 14px;
        letter-spacing: 1.2px;
        &__logo {
            display: flex;
            flex-wrap: wrap;
            img {
                padding-left: 25px;
                height: 75px;
            }
            p {
                padding: 10px 0;
                padding-left: 25px;
                a {
                    &:hover {
                        text-decoration: underline;
                    }
                }
            }
        }
        &__support {
            text-align: right;
            padding-left: 25px;
            .lg-block({
                    text-align: left;
            });
            span {
                color: #b6d9d7;
                letter-spacing: 0.28px;
                text-decoration: underline;
                
            }
        }
    }
}