@import 'smart-grid.less';
@import 'fonts.less';
@import 'common.less';
@import 'intro.less';
@import 'work.less';
@import 'justice.less';
@import 'for.less';
@import 'plus.less';
@import 'money.less';
@import 'develop.less';
@import 'guarant.less';
@import 'reviews.less';
@import 'portfolio.less';
@import 'footer.less';

.reset();
.container {
    .container();
    position: relative;
}
html {
    font-size: 16px;
}
a {
    text-decoration: none;
    color: inherit;
}
a:hover {
    text-decoration: none;
}
body {
    font-family: @regular, sans-serif;
    overflow-x: hidden;
}
p {
    line-height: 24px;
}
ul,li {
    margin: 0;
    padding: 0;
}
li {
    list-style: none;
}
:active, :hover, :focus {
    outline: 0;
    outline-offset: 0;
}
input {
    border: none;
}
button {
    background: none;
    border: none;
}