.portfolio {
    background: url(../img/portfolioBG.png) no-repeat;
    background-size: cover;
    background-position: center center;
    min-height: 1354px;
    position: relative;
    margin-top: 50px;
    color: #fff;
    .lg-block({
        min-height: auto;
        background: none;
        background-image: @gradient;
    });
    &_title {
        .lg-block({
           margin-top: -80px;
        });
        .md-block({
            margin-top: 0;
        });
    }
    &_top {
        .row-flex();
        padding-top: 300px;
        .lg-block({
            padding-top: 200px;
        });
        .md-block({
            padding-top: 50px;
        });
        .sm-block({
            justify-content: center;
        });
        &__img {
            .col();
            .size(6);
            .size-sm(12);
            position: relative;
            .sm-block({
                order: 2;
            });
        }
        &__item {
            position: absolute;
            text-align: center;
            .md-block({
                position: relative;
            });
            h5 {
                color: #000;
                font-family: @bold, sans-serif;
                letter-spacing: 1.6px;
                position: absolute;
                bottom: 100px;
                left: 50%;
                transform: translate(-50%,0);
            }
            a {
                position: absolute;
                bottom: 55px;
                left: 50%;
                transform: translate(-50%);
                color: #051a3a;
                text-decoration: underline;
                font-family: @bold, sans-serif;
            }
            &:nth-child(1) {
                background: url(../img/port1.png) no-repeat;
                width: 392px;
                height: 283px;
                top: -140px; left: 35px;
                z-index: 10;
                .md-block({
                    top: auto; left: auto;
                });
                .xs-block({
                    width: auto;
                    background-size: cover;
                });
            }
            &:nth-child(2) {
                background: url(../img/port2.png) no-repeat;
                width: 310px;
                height: 312px;
                top: 5px; left: 280px;
                .md-block({
                    top: auto; left: auto;
                });
                .xs-block({
                    width: auto;
                    background-size: cover;
                });
            }
        }
        &__text {
            .col();
            .size(6);
            .size-sm(12);
            .sm-block({
                order: 1;
            });
            p {
                .sm-block({
                    float: none;
                    text-align: center;
                    margin: 35px auto 0;
                });
                margin-top: 100px;
                float: right;
                max-width: 350px;
                letter-spacing: 1.6px;
                &:last-child {
                    margin-top: 30px;
                }
                .lg-block({
                    margin-top: 50px;
                });
            }
        }
    }
    &_bottom {
        margin-top: 30px;
        position: relative;
        .lg-block({
            display: flex;
            flex-wrap: wrap;
        });
        .sm-block({
            justify-content: center;
        });
        &__item {
            position: absolute;
            text-align: center;
            .lg-block({
                position: relative;
            });
            .xs-block({
                width: auto;
                background-size: cover;
            });
            h5 {
                color: #000;
                font-family: @bold, sans-serif;
                letter-spacing: 1.6px;
                position: absolute;
                bottom: 100px;
                left: 50%;
                transform: translate(-50%,0);
            }
            a {
                position: absolute;
                bottom: 55px;
                left: 50%;
                transform: translate(-50%);
                color: #051a3a;
                text-decoration: underline;
                font-family: @bold, sans-serif;
            }
            &:nth-child(1) {
                background: url(../img/port3.png) no-repeat;
                width: 395px;
                height: 263px;
                top: 0px; left: 35px;
                z-index: 10;
                .lg-block({
                    top: auto; left: auto;
                });
                
            }
            &:nth-child(2) {
                background: url(../img/port4.png) no-repeat;
                width: 434px;
                height: 372px;
                top: 130px; left: 270px;
                .lg-block({
                    top: auto; left: auto;
                });

            }
            &:nth-child(3) {
                background: url(../img/port5.png) no-repeat;
                width: 315px;
                height: 272px;
                top: 350px; right: 305px;
                z-index: 10;
                .lg-block({
                    top: auto; right: auto;
                });

            }
            &:nth-child(4) {
                background: url(../img/port6.png) no-repeat;
                width: 434px;
                height: 300px;
                top: 170px; right: 20px;
                .lg-block({
                    top: auto; right: auto;
                });

            }
        }
    }
}
.portfolio .container {
    overflow: inherit;
}