.btn-up {
  display: block;
  cursor: pointer;
  position: absolute;
  left: 50%;
  transform: translate(0, 50%);
  bottom: 150px;
  width: 85px;
  height: 85px;
  border-radius: 50%;
  background: #fff;
  z-index: 100;
  outline: 0!important;
  transition: .4s;
}

.btn-up::after {
  content: '';
  width: 12px;
  height: 12px;
  border-top: 3px solid #2d2d2d;
  border-left: 3px solid #2d2d2d;
  transform: rotate(225deg);
  position: absolute;
  top: 0px;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
  transition: .5s all;
}

.btn-up:hover::after {
  top: 5px;
}

.btn-up::before {
  content: "";
  position: absolute;
  border: 8px solid #eee;
  left: -5px;
  right: -5px;
  top: -5px;
  bottom: -5px;
  border-radius: 50%;
  animation: pulse 2s linear infinite;
}

@keyframes pulse {
  0% {
    transform: scale(0.8);
    opacity: 0;
  }

  50% {
    opacity: 1;
  }

  100% {
    transform: scale(1.2);
    opacity: 0;
  }
}

