  .popup {
    background: none;
    background-color: #d3dcde;
  }

  .intro {
    height: 1000px;
  }

  .intro .header_top {
    flex-wrap: wrap;
    justify-content: center;
  }

  .intro_left__descr {
    margin-top: 25px;
  }

  .intro_left__block img {
    width: 240px;
    height: 380px;
  }

  .intro_left__block ul {
    margin-top: 5px;
  }

  .work {
    margin-top: 0;
  }

  .plus_content {
    justify-content: center;
  }

  .plus_text {
    width: calc(66.66666666666667% - 15px);
  }

  .plus_photo {
    margin-top: 20px;
    width: calc(83.33333333333334% - 15px);
    text-align: center;
  }

  .money_text {
    width: calc(100% - 15px);
    padding: 0 35px;
  }

  .money_text__bottom {
    flex-wrap: wrap;
  }

  .money_logo {
    display: none;
  }

  .develop_top {
    justify-content: center;
  }

  .develop_top__ul {
    width: calc(83.33333333333334% - 15px);
  }

  .develop_top__img {
    display: none;
  }

  .develop_bottom {
    justify-content: center;
    margin-left: 5px;
    margin-top: -20px;
  }

  .develop_bottom__ul {
    width: calc(83.33333333333334% - 15px);
    margin-left: 0;
    order: 0;
  }

  .develop_bottom__img {
    display: none;
  }

  .portfolio_top {
    justify-content: center;
  }

  .portfolio_top__img {
    width: calc(100% - 15px);
  }

  .portfolio_top__img {
    order: 2;
  }

  .portfolio_top__text {
    width: calc(100% - 15px);
  }

  .portfolio_top__text {
    order: 1;
  }

  .portfolio_top__text p {
    float: none;
    text-align: center;
    margin: 35px auto 0;
  }

  .portfolio_bottom {
    justify-content: center;
  }

  .footer_content {
    justify-content: center;
  }

  .footer_text {
    width: calc(66.66666666666667% - 15px);
  }

  .footer_form {
    width: calc(66.66666666666667% - 15px);
  }

  .footer_form {
    margin-top: 25px;
  }

  .container {
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media screen and (max-width: 600px) {
  .popup {
    width: 100%;
    height: 100vh;
    font-size: 15px;
  }
}

@media screen and (max-width: 480px) {
  .popup_top {
    font-size: 16px;
    letter-spacing: 1;
    padding-left: 25px;
    line-height: 1.2;
  }

  .popup_radio + i {
    margin-right: 5px;
  }

  .popup_input + i {
    margin-right: 5px;
  }

  .popup-last_title {
    font-size: 16px;
    letter-spacing: 1px;
    line-height: 1.2;
  }

  .intro {
    height: 1120px;
  }

  .intro .header_bottom {
    padding-top: 15px;
  }

  .intro_left__block img {
    display: none;
  }

  .intro_left__block ul li:last-child button {
    margin: 0 auto;
  }

  .work_bottom {
    margin-top: 0px;
  }

  .work_bottom__btn {
    padding: 15px;
    font-size: 14px;
  }

  .reviews_item {
    margin: 0;
  }

  .portfolio_top__item:nth-child(1) {
    width: auto;
    background-size: cover;
  }

  .portfolio_top__item:nth-child(2) {
    width: auto;
    background-size: cover;
  }

  .portfolio_bottom__item {
    width: auto;
    background-size: cover;
  }

  .footer_text {
    width: calc(83.33333333333334% - 15px);
  }

  .footer_form {
    width: calc(83.33333333333334% - 15px);
  }

  .container {
    padding-left: 5px;
    padding-right: 5px;
  }
}

@media screen and (max-width: 360px) {
  .white_t {
    font-size: 24px;
  }

  .blue_t {
    font-size: 24px;
  }

  .popup_content {
    padding: 20px 25px 0;
  }

  .popup_text {
    font-size: 15px;
    line-height: 1.2;
  }

  .popup_label {
    font-size: 14px;
  }

  .popup .popup-last_form .footer_form__submit {
    padding: 5px 15px;
  }

  .work {
    position: relative;
    top: -50px;
  }

  .work_bottom {
    position: relative;
    top: -100px;
  }

  .footer_text {
    width: calc(100% - 15px);
  }

  .footer_form {
    width: calc(100% - 15px);
  }

  .container {
    padding-left: 5px;
    padding-right: 5px;
  }
}