.plus {
    margin-top: 150px;
    .md-block({
        margin-top: 100px;
    });
    &_title {
        &::after {
            content: 'Плюсы нашего ПО';
            .h2;
            .md-block({
                display: none;
            });
        }
    }
    &_content {
        margin-top: 80px;
        .row-flex();
        justify-content: space-between;
        .md-block({
            justify-content: space-around;
            margin-top: 50px;
        });
        .sm-block({
           justify-content: center;
        });
    }
    &_text {
        .col();
        .size(7);
        .md-block({
            .size(5);
        });
        .sm-block({
            .size(8);
        });
        &__ul {
            
        }
        &__li {
            position: relative;
            padding-top:10px;
            margin: 10px 0 15px;
            &::after {
                content: '';
                width: 34px;
                height: 5px;
                background-color: @main;
                position: absolute;
                top: 0;
                left: 0;
            }
        }
    }
    &_photo {
        .col();
        .size(4);
        .md-block({
            .size(5);
        });
        .sm-block({
            margin-top: 20px;
            .size(10);
            text-align: center;
        });
        &__img {
            box-shadow: -5px 5px 5px 0px rgba(0,0,0,0.75);
        }
        &__p {
            font-family: @bold, sans-serif;
            font-size: 18px;
            margin-top: 10px;
            text-align: center;
        }
    }
}