.for {
    .lg-block({
        margin-top: 100px;
    });
    &_title {
        &::after {
            content: 'Мы работаем';
            .h2;
            .md-block({
                display: none;
            });
        }
    }
    &_content {
        margin-top: 100px;
        display: flex;
        justify-content: space-between;
        .md-block({
            flex-wrap: wrap;
            justify-content: center;
            margin-top: 40px;
        });
    }
    &_item {
        margin: 10px 0;
        max-width: 250px;
        padding-left: 50px;
        padding-right: 15px;
        position: relative;
        &::after {
            content: '';
            position: absolute;
            top: 5px; left: 0;
            background: url(../img/for_icon.png) no-repeat;
            width: 32px;
            height: 28px;
        }
    }
}