.money {
    background: url(../img/moneyBG.png) no-repeat;
    background-size: cover;
    background-position: center center;
    min-height: 829px;
    margin-top: -50px;
    position: relative;
    color: #fff;
    .lg-block({
        min-height: auto;
        background: none;
        background-image: @gradient;
        margin-top: 50px;
        padding-bottom: 45px;
    });
    &_title {
        padding: 245px 0 10px;
        .lg-block({
            padding-top: 50px;     
        });
    }
    &_content {
        margin-top: 45px;
        .row-flex();
        justify-content: center;
    }
    &_text {
        .col();
        .size(6);
        .lg-block({
            .size(7);
        });
        .md-block({
            .size(8);
        });
        .sm-block({
            .size(12);
            padding: 0 35px;
        });
        line-height: 30px;
        letter-spacing: 1.6px;
        &__p {
            max-width: 300px;
        }
        &__bottom {
            margin-top: 50px;
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            .sm-block({
                flex-wrap: wrap;
            });
        }
        &__btn {
            display: block;
            padding: 15px 70px;
            border-radius: 25px;
            color: #000;
            font-family: @bold, sans-serif;
            background-color: #fff;
            transition: .3s all;
            &:hover {
                transform: translateY(-4px);
            }
            .lg-block({
                padding: 15px 45px;
            });
            .md-block({
                padding: 15px 40px;
            });
        }
        &__test {
            max-width: 280px;
            padding: 0 5px;
            .md-block({
                max-width: 250px;
            });
        }
    }
    &_logo {
        padding-top: 25px;
        .col();
        .size(4);
        .md-block({
            .size(3);
        });
        .sm-block({
            display: none;
        });
    }
}