@gradient : linear-gradient(-45deg, #116577 20%, #5df886 220%, #5ef986 100%);
@main: #48908b;

.submit {
    padding: 10px 18px;
    box-shadow: 3px 4px 10px 1px rgba(0, 0, 0, 0.3);
    border-radius: 25px;
    background-color: #fff;
    line-height: 30px;
    font-family: @bold, sans-serif;
    transition: .3s all;
    cursor: pointer;
    &:hover {
        transform: translateY(-4px);
    }
}

.h2 {
    position: absolute;
    top: -20px; left: 50%;
    transform: translate(-50%);
    color: #f7f7f7;
    font-family: @bold;
    font-size: 100px;
    letter-spacing: 12px;
    z-index: -1;
    width: 100%;
}

.white_t {
    font-family: @bold, sans-serif;
    font-size: 30px;
    letter-spacing: 3px;
    text-align: center;
    .xxs-block({
        font-size: 24px;
    });
}
.blue_t {
    color: @main;
    font-family: @bold, sans-serif;
    font-size: 30px;
    letter-spacing: 3px;
    text-align: center;
    .xxs-block({
        font-size: 24px;
    });
}
.btn-up {
    display: block;
    cursor: pointer;
    position: absolute;
    left: 50%; transform: translate(0,50%);
    bottom: 150px;
    width: 85px;
    height: 85px;
    border-radius: 50%;
    background: #fff;
    z-index: 100;
    outline: 0!important;
    transition: .4s;
    .md-block({
        width: 60px;
        height: 60px;
    });
    .sm-block({
        display: none;
    });
}
.btn-up::after {
    content: '';
    width: 12px;
    height: 12px;
    border-top: 3px solid #2d2d2d;
    border-left: 3px solid #2d2d2d;
    transform: rotate(225deg);
    position: absolute;
    top: 0px; bottom: 0;
    right: 0; left: 0;
    margin: auto;
    transition: .5s all;
}
.btn-up:hover::after {
    top: 5px;
}
.btn-up::before {
    content: "";
    position: absolute;
    border: 8px solid #eee;
    left: -5px; right: -5px;
    top: -5px; bottom: -5px;
    border-radius: 50%;
    animation: pulse 2s linear infinite;
}
@keyframes pulse {
    0% {
        transform: scale(0.8);
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    100% {
        transform: scale(1.2);
        opacity: 0;
    }
}
.btn-lg {
    .lg-block({
       display: none;
    });
}
.btn-guarant {
   bottom: 170px;
}
.overlay {
    position: fixed;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, .8);
    display: none;
    z-index: 1000;
}

.popup {
    position: fixed;
    width: 800px;
    min-height: 500px;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    box-sizing: border-box;
    color: #fff;
    background: url(../img/popupBG.png) no-repeat;
    background-size: cover;
    background-position: center center;
    .sm-block({
        background: none;
        background-color: #d3dcde;
    });
    &_top {
        background-color: #116677;
        box-shadow: 3px 4px 10px 1px rgba(0, 0, 0, 0.3);
        padding-top: 17px;
        padding-left: 83px;
        padding-bottom: 10px;
        font-size: 18px;
        letter-spacing: 0.9px;
        line-height: 30px;
        .xs-block({
            font-size: 16px;
            letter-spacing: 1;
            padding-left: 25px;
            line-height: 1.2;
        });
    }
    &_content {
        padding-left: 50px;
        padding-right: 43px;
        padding-top: 39px;
        .xxs-block({
            padding: 20px 25px 0;
        });
    }
    &_number {
        color: #000;
        font-family: @bold;
        font-size: 18px;
        line-height: 24px;
        letter-spacing: 1.35px;
    }
    &_text {
        margin: 18px 0 10px;
        color: #000;
        font-family: @bold, sans-serif;
        font-size: 18px;
        line-height: 24px;
        letter-spacing: 1.35px;
        .xxs-block({
            font-size: 15px;
            line-height: 1.2;
        });
    }
    &_textarea {
        width: 100%;
        height: 180px;
        border: 1px solid #aeaeae;
        padding: 10px;
    }
    &_label {
        position: relative;
        display: block;
        color: #000;
        padding: 7px 0;
        .xxs-block({
            font-size: 14px;
        });
    }
    &_radio {
        display: none;
        &+i {
            margin-right: 25px;
            cursor: pointer;
            display: inline-block;
            width: 18px;
            height: 18px;
            border: 1px solid #000;
            border-radius: 50%;
            position: relative;
            .xs-block({
               margin-right: 5px;
            });
            &::before {
                content: '';
                position: absolute;
                top: 4px; bottom: 4px;
                right: 4px; left: 4px;
                width: 10px; height: 10px;
                transition: .3s all;
                transform: scale(0);
                background-color: #000;
                border-radius: 50%;
            }
        }
        
    }
    .wrap {
        display: flex;
        justify-content: space-between;
    }
    &_input {
        display: none;
        &+i {
            margin-right: 25px;
            cursor: pointer;
            display: inline-block;
            width: 17px;
            height: 17px;
            border: 1px solid #000;
            position: relative;
            .xs-block({
                margin-right: 5px;
            });
            &::before {
                content: '';
                position: absolute;
                top: 4px; bottom: 4px;
                right: 4px; left: 4px;
                width: 9px; height: 9px;
                transition: .3s all;
                transform: scale(0);
                background-color: #000;
            }
        }
    }
    &_btn {
        width: 100%;
        display: flex;
        bottom: 60px;
        align-items: center;
        justify-content: space-between;
        position: absolute;
    }
    &_back {
        position: absolute;
        left: 50px;
        cursor: pointer;
        color: #116677;
        line-height: 24px;
        text-decoration: underline;
    }
    &_next {
        position: absolute;
        right: 53px;
        cursor: pointer;
        text-align: center;
        color: #fff;
        font-family: @bold, sans-serif;
        font-size: 18px;
        line-height: 24px;
        padding-right: 65px;
        padding-bottom: 12px;
        padding-left: 35px;
        padding-top: 12px;
        box-shadow: 3px 4px 10px 1px rgba(0, 0, 0, 0.3);
        border-radius: 10px;
        background-color: #116677;
        &::after {
            content: '';
            position: absolute;
            right: 12px; top: 8px;
            background: url(../img/popupArrow.png) no-repeat;
            width: 33px;
            height: 33px;
        }
    }
}

.close-popup {
    position: absolute;
    top: 15px;
    right: 15px;
    width: 23px;
    height: 23px;
    cursor: pointer;
}

.close-popup:before {
    content: '';
    background-color: #fff;
    position: absolute;
    height: 1px;
    width: 31px;
    top: 11px;
    left: -4px;
    transform: rotate(-45deg);
}

.close-popup:after {
    content: '';
    background-color: #fff;
    position: absolute;
    height: 1px;
    width: 31px;
    top: 11px;
    transform: rotate(45deg);
    left: -4px;
}

@media screen and (max-width: 600px) {

    .popup {
        width: 100%;
        height: 100vh;
        font-size: 15px;
    }
}
.slide-item {
    display: none;
}
.slide-item:nth-child(1) {
    display: block;
}
input[type='radio']:checked+i::before {
    transform: scale(1);
}
input[type='checkbox']:checked+i::before {
    transform: scale(1);
}
.popup-last_title {
    color: #000;
    text-align: center;
    letter-spacing: 1.36px;
    font-size: 18px;
    font-family: @bold, sans-serif;
    .xs-block({
        font-size: 16px;
        letter-spacing: 1px;
        line-height: 1.2;
    });
    
}
.popup .popup-last_form .footer_form__submit {
    .xxs-block({
        padding: 5px 15px;
    });
}
