.intro {
    background: url(../img/intro.png) no-repeat;
    background-size: cover;
    background-position: center center;
    color: #fff;
    min-height: 800px;
    .sm-block({
        height: 1000px;
    });
    .xs-block({
        height: 1120px;
    });
    
    .header {
        &_top {
            position: absolute;
            right: 0; top: 20px;
            z-index: 10;
            .md-block({
                position: static;
                display: flex;
                justify-content: space-between;
                padding-top: 20px;
            });
            .sm-block({
                flex-wrap: wrap;
                justify-content: center;
            });
            &__social {
                margin-right: 60px;
                float: left;
                .md-block({
                    margin-right: 0;
                    padding-bottom: 20px;
                });
                &--item {
                    display: inline-block;
                    margin: 0 15px;
                    transition: .3s all;
                    &:hover {
                        transform: translateY(-4px);
                    }
                    &:nth-child(1) {
                        background: url(../img/vk.png) no-repeat;
                        width: 27px;
                        height: 16px;
                    }
                    &:nth-child(2) {
                        background: url(../img/viber.png) no-repeat;
                        width: 22px;
                        height: 22px;
                    }
                    &:nth-child(3) {
                        background: url(../img/telega.png) no-repeat;
                        width: 22px;
                        height: 18px;
                    }
                    &:nth-child(4) {
                        background: url(../img/inst.png) no-repeat;
                        width: 23px;
                        height: 22px;
                    }
                }
            }
            &__tel {
                font-size: 18px;
                &:hover {
                    text-decoration: underline;
                }
            }
        }
        &_bottom {
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding-top: 50px;
            position: relative;
            .md-block({
                padding-top: 5px;
            });
            .xs-block({
                padding-top: 15px;
            });
            &__logo {
                font-size: 14px;
                color: #000;
                font-family: @bold, sans-serif;
                transition: .3s all;
                cursor: pointer;
                &:hover {
                    transform: translateY(-4px);
                }
                &--text {
                    transition: .3s all;
                    margin-top: 8px;   
                }
            }
            &__nav {
                position: relative;
                top: -10px;
                .md-block({
                    opacity: 0;
                    display: flex;
                    flex-direction: column;
                    width: 130px;
                    position: absolute;
                    top: 95px; right: 0;
                    z-index: 10;
                    background: @main;
                    transition: all .3s;
                    padding: 10px 15px;
                    border-radius: 5px;
                });
                &--item {
                    margin: 0 25px;
                    padding: 10px 0;
                    transition: .3s all;
                    &:last-child {
                        margin-right: 0;
                    }
                    &:first-child {
                        margin-left: 0;
                    }
                    &:hover {
                        border-top: 2px solid #fff;
                        border-bottom: 2px solid #fff;
                    }
                    .lg-block({
                        margin: 0 15px;
                    });
                    .md-block({
                        display: inline;
                        margin: 10px auto;
                        padding: 5px;
                        &:hover {
                            border: none;
                        } 
                        &:last-child {
                            margin: 10px auto;
                        }
                        &:first-child {
                            margin: 10px auto;
                        }
                    });
                }
            }
        }
    }
    
    &_content {
        .shift-left(1);
        margin-top: 30px;
        display: flex;
        justify-content: center;
        .lg-block({
            .shift-left(0);
        });
        .md-block({
            flex-wrap: wrap;
        });
    }
    &_left {
        &__title {
            font-size: 25px;
            font-family: @bold, sans-serif;
            line-height: 48px;
            max-width: 570px;
            text-align: center;
            padding: 0 10px;
            margin: 0 auto;
        }
        &__descr {
            margin: 35px auto 0;
            letter-spacing: 1.8px;
            font-size: 18px;
            text-align: center;
            max-width: 540px;
            .sm-block({
                margin-top: 25px;
            });
        }
        &__block {
            margin-top: 35px;
            display: flex;
            
            img {
                width: 100%;
                height: auto;
                max-width: 339px;
                transition: .3s all;
                .sm-block({
                    width: 240px;
                    height: 380px;
                });
                .xs-block({
                    display: none;
                });
                &:hover {
                    transform: translateY(-4px);
                }
            }
            ul {
                margin-top: 35px;
                .sm-block({
                   margin-top: 5px;
                });
                li {
                    position: relative;
                    padding: 10px 0 25px;
                    &:last-child {
                        display: none;
                        .md-block({
                            display: block;
                        });
                        
                        button {
                            .xs-block({
                                margin: 0 auto;
                            });
                        }
                    }
                    &:last-child::before {
                        display: none;
                    }
                    &:hover::before {
                        width: 40px;
                    }
                    &::before{
                        transition: .3s all;
                        content: '';
                        position: absolute;
                        top: 0; left: 0;
                        width: 31px;
                        height: 2px;
                        background-color: #ffffff;
                    }
                    p {
                        font-family: @bold, sans-serif;
                        font-size: 16px;
                        line-height: 24px;
                        letter-spacing: 1.6px;
                    }
                }
            }
        }
    }
    &_right {
        .md-block({
            display: none;
        });
        width: 457px;
        height: 460px;
        border-radius: 10px;
        background: rgba(255,255,255,0.5);
        &__form {
            position: relative;
            padding: 25px 10px 0px;
            &--title {
                text-align: center;
                color: #010101;
                font-family: @medium, sans-serif;
                font-size: 18px;
                line-height: 30px;
                .lg-block({
                    font-size: 16px;
                });
            }
        }
        &__label {
            margin: 25px 0 10px;
            padding: 0 40px;
            display: block;
            color: #060606;
            font-size: 16px;
            line-height: 30px;
            .lg-block({
                font-size: 14px;
                margin-top: 20px;
            });
        }
        &__input {
            width: 70%;
            margin-left: 40px;
            border: 1px solid #aeaeae;
            background-color: #fafafa;
            font-size: 14px;
            padding: 15px;
            color: #1f1f1f;
        }
        &__submit {
            margin: 25px auto 0;
            display: block;
        }
    }
}

.btn_mnu {
    .md-block({
        opacity: 1;
    });
    cursor: pointer;
    opacity: 0;
    width: 50px;
    height: 50px;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    background-color: transparent;
    position: absolute;
    right: 0;
    margin-top: 5px;
}
.btn_row {
    height: 2px;
    width: 60%;
    margin: 2px 0;
    background-color: #fff;
    position: relative;
    -webkit-transition: all .5s;
    -o-transition: all .5s;
    transition: all .5s;
}
.btn_mnu.active .btn_row:first-child {
    -webkit-transform: rotate(-135deg);
    -ms-transform: rotate(-135deg);
    transform: rotate(-135deg);
    top: 6px;
}
.btn_mnu.active .btn_row:last-child {
    -webkit-transform: rotate(-225deg);
    -ms-transform: rotate(-225deg);
    transform: rotate(-225deg);
    top: -6px;
}
.btn_mnu.active .btn_row:nth-child(2) {
    opacity: 0;
}
.header_bottom .header_bottom__nav.active {
    opacity: 1;
}
