.reviews {
    .lg-block({
        margin-top: 50px;
    });
    &_title {
        &::after {
            content: 'Отзывы';
            .h2;
            .md-block({
                display: none;
            });
        }
    }
    &_content {
        margin-top: 100px;
    }
    &_item {
        margin: 0 50px;
        .xs-block({
            margin: 0;
        });
    }
    img {
        opacity: 0.5;
        transition: all 300ms ease;
        max-height: 450px;
    }
    .slick-center img {
        opacity: 1;
        transform: scale(1.2);
    }
    .slick-next {
        right: 5px;
        z-index: 100;
        background: url(../img/arrows-next.png) no-repeat;
        width: 22px;
        height: 34px;
        &::before {
            display: none;
        }
        &:hover{
            background: url(../img/arrows-next.png) no-repeat;
        }
    }
    .slick-prev {
        z-index: 100;
        left: 30px;
        background: url(../img/arrows-prev.png) no-repeat;
        width: 22px;
        height: 34px;
        &::before {
            display: none;
        }
        &:hover{
            background: url(../img/arrows-prev.png) no-repeat;
        }
    }
}