.develop {
    margin-top: -50px;
    .lg-block({
        margin-top: 50px;
    });
    &_title {
        &::after {
            content: 'Разработка';
            .h2;
            .md-block({
                display: none;
            });
        }
    }
    &_top {
        margin-top: 60px;
        margin-left: 70px;
        .row-flex();
        align-items: center;
        .md-block({
            margin-left: 0;
        });
        .sm-block({
            justify-content: center;
        });
        &__ul {
            .col();
            .size(4);
            .md-block({
                .size(6);
            });
            .sm-block({
                .size(10);
            });
        }
        &__li {
            margin: 20px 0;
            padding: 10px 0;
            position: relative;
            padding-left: 100px;
            &::after {
                content: '';
                position: absolute;
                top: 0; left: 0;
                background: url(../img/dev1.png) no-repeat;
                width: 63px;
                height: 57px;
            }
            &:nth-child(2) {
                &::after {
                    background: url(../img/dev2.png) no-repeat;
                }
            }
            &:nth-child(3) {
                &::after {
                    background: url(../img/dev3.png) no-repeat;
                }
            }
            &:nth-child(4) {
                &::after {
                    background: url(../img/dev4.png) no-repeat;
                }
            }
        }
        &__img {
            
            .col();
            .size(7);
            .md-block({
                .size(6);
            });
            .sm-block({
                display: none;
            });
        }
    }
    &_bottom {
        margin-right: 70px;
        .row-flex();
        align-items: center;
        .md-block({
            margin-right: 0px;
        });
        .sm-block({
            justify-content: center;
            margin-left: 5px;
            margin-top: -20px;
        });
        &__ul {
            margin-left: 20px;
            order: 2;
            .col();
            .size(4);
            .md-block({
                .size(6);
            });
            .sm-block({
                .size(10);
                margin-left: 0;
                order: 0;
            });
        }
        &__li {
            margin: 20px 0;
            padding: 10px 0;
            position: relative;
            padding-left: 100px;
            &::after {
                content: '';
                position: absolute;
                top: 0; left: 0;
                background: url(../img/dev5.png) no-repeat;
                width: 63px;
                height: 57px;
            }
            &:nth-child(2) {
                &::after {
                    background: url(../img/dev6.png) no-repeat;
                }
            }
            &:nth-child(3) {
                &::after {
                    background: url(../img/dev7.png) no-repeat;
                }
            }
            &:nth-child(4) {
                &::after {
                    background: url(../img/dev8.png) no-repeat;
                }
            }
        }
        &__img {
            order: 1;
            .col();
            .size(7);
            .md-block({
                .size(6);
            });
            .sm-block({
                display: none;
            });
        }
    }
}