.justice {
    margin-top: -150px;
    padding-bottom: 45px;
    min-height: 891px;
    background: url(../img/bg2top.png) no-repeat;
    background-size: cover;
    background-position: center center;
    position: relative;
    color: #fff;
    text-align: center;
    .lg-block({
        min-height: auto;
        background: none;
        background-image: @gradient;
    });
    &_title {
        padding-top: 300px;
        .lg-block({
            padding-top: 50px;
        });
    }
    &_block {
        margin-top: 70px;
        display: flex;
        justify-content: space-around;
        .lg-block({
            flex-wrap: wrap;
            justify-content: center;
        });
        &__item {
            margin:20px;
            padding: 0 10px;
            border-radius: 10px;
            background: url(../img/just.png) no-repeat;
            position: relative;
            &:nth-child(1)::after {
                content: '';
                position: absolute;
                top: 30px; right: 30px;
                background: url(../img/iconstop1.png) no-repeat;
                width: 109px;
                height: 109px;
            }
            &:nth-child(2)::after {
                content: '';
                position: absolute;
                top: 30px; right: 30px;
                background: url(../img/iconstop2.png) no-repeat;
                width: 109px;
                height: 109px;
            }
            &:nth-child(3)::after {
                content: '';
                position: absolute;
                top: 30px; right: 30px;
                background: url(../img/iconstop3.png) no-repeat;
                width: 109px;
                height: 109px;
            }
            &:nth-child(4)::after {
                content: '';
                position: absolute;
                top: 30px; right: 30px;
                background: url(../img/iconstop4.png) no-repeat;
                width: 109px;
                height: 109px;
            }
        }
        &__img {

            padding: 50px 0 35px;
            
        }
        &__text {
            max-width: 250px;
            line-height: 24px;
            letter-spacing: 1.6px;
        }
    }
}
.justice .btn-up {
    bottom: 120px;
    .lg-block({
        bottom: 0px;
    });
}
